<template>
  <div class="main-tab-content">
    <!-- & -->
    <el-row>
      <el-col :span="20">
        <el-form :inline="true"
                 :model="searchForm"
                 size="mini">
          <el-form-item label="">
            <el-select v-model="searchForm.subjectId"
                       name="subjectId">
              <el-option v-for="(item, index) in subjectList"
                         :key="index"
                         :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="searchForm.modelTypeId"
                       placeholder=""
                       name="modelTypeId">
              <el-option v-for="(item, index) in tplTypeList"
                         :key="index"
                         :label="item.modelTypeName"
                         :value="item.modelTypeId"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <!--  -->
      <el-col :span="4"
              class="clearfix">
        <div class="float-r">
          <el-button type="primary"
                     size="mini"
                     @click="onSearch"></el-button>
          <el-button type="primary"
                     plain
                     size="mini"
                     @click="dialogFormVisible = true"></el-button>
        </div>
      </el-col>
    </el-row>
    <!--  -->
    <el-table v-loading="tableLoading"
              element-loading-spinner="el-icon-loading"
              element-loading-text=""
              :data="tableData"
              :height="tableHeight"
              stripe
              style="width: 100%">
      <el-table-column prop="modelTypeName"
                       label=""
                       width="180"
                       align="center">
      </el-table-column>
      <el-table-column prop="versions"
                       label=""
                       width="180"
                       align="center">
        <template slot-scope="scope">
          <el-select v-model="scope.row.selected"
                     :name="scope.row.rowCode"
                     size="mini"
                     @change="switchVersion(scope)">
            <el-option v-for="(item, index) in scope.row.versionList"
                       :key="index"
                       :label="item.name"
                       :value="item.id"></el-option>
          </el-select>
        </template>
      </el-table-column>
      <el-table-column prop="isOfficial"
                       label=""
                       align="center">
        <template slot-scope="scope">
          <i class="color-primary iconfont icon-shi"
             v-if="scope.row.isOfficial"></i>
          <i class="color-primary iconfont icon-fou"
             v-else></i>
        </template>
      </el-table-column>
      <el-table-column prop="isDefault"
                       label=""
                       align="center">
        <template slot-scope="scope">
          <i class="color-primary iconfont icon-shi"
             v-if="scope.row.isDefault"></i>
          <i class="color-primary iconfont icon-fou"
             v-else></i>
        </template>
      </el-table-column>
      <el-table-column prop="reviser"
                       label=""
                       align="center">
      </el-table-column>
      <el-table-column prop="revisionTime"
                       label=""
                       align="center">
      </el-table-column>
      <el-table-column prop="effectiveTime"
                       label=""
                       align="center">
      </el-table-column>
      <el-table-column prop="operate"
                       label=""
                       width="240"
                       align="center">
        <template slot-scope="scope">
          <el-tooltip effect="dark"
                      content=""
                      placement="top">
            <el-button type="warning"
                       icon="el-icon-edit"
                       size="mini"
                       @click="eidtVersion(scope)"
                       :disabled="!scope.row.selected"></el-button>
          </el-tooltip>
          <el-tooltip effect="dark"
                      content=""
                      placement="top">
            <el-button type="danger"
                       icon="el-icon-delete"
                       size="mini"
                       @click="deleteVersion(scope)"
                       :disabled="!scope.row.selected"></el-button>
          </el-tooltip>
          <el-tooltip effect="dark"
                      content=""
                      placement="top">
            <el-button type="success"
                       icon="el-icon-download"
                       size="mini"
                       @click="downloadTpl(scope)"
                       :disabled="!scope.row.selected"></el-button>
          </el-tooltip>
          <el-button type="primary"
                     size="mini"
                     @click="setDefault(scope)"
                     :disabled="Boolean(scope.row.isDefault) || !Boolean(scope.row.isOfficial) || !scope.row.selected"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog width="30%"
               title=""
               :close-on-click-modal="false"
               :close-on-press-escape="false"
               @opened="resetForm('importForm')"
               :visible.sync="dialogFormVisible">
      <el-form ref="importForm"
               size="small"
               :model="importForm"
               label-width="70px">
        <el-form-item prop="subjectId"
                      label="">
          <el-select v-model="importForm.subjectId">
            <el-option v-for="(item, index) in subjectList"
                       :key="index"
                       :label="item.name"
                       :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="modelTypeId"
                      label="">
          <el-select v-model="importForm.modelTypeId"
                     :disabled="importTplTypeDisabled">
            <el-option v-for="(item, index) in importTplTypeList"
                       :key="index"
                       :label="item.modelTypeName"
                       :value="item.modelTypeId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="fileNameId"
                      label="">
          <el-select v-model="importForm.fileNameId"
                     :disabled="importTplFileListDisabled">
            <el-option v-for="(item, index) in importTplFileList"
                       :key="index"
                       :label="item.name"
                       :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer"
           style="text-align:center">
        <el-button size="small"
                   @click="dialogFormVisible = false"> </el-button>
        <el-button size="small"
                   type="primary"
                   @click.stop="confirmImport"
                   :disabled="importDisableState"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog center
               v-if="dialogUploadVisible"
               :visible.sync="dialogUploadVisible"
               width="30%"
               title=""
               :close-on-click-modal="false"
               :close-on-press-escape="false"
               v-loading="isUploading"
               element-loading-spinner="el-icon-loading"
               element-loading-text="">
      <div slot="footer"
           class="dialog-footer">
        <el-button size="small"
                   @click="dialogUploadVisible = false"
                   :disabled="isUploading"> </el-button>
        <!--  -->
        <el-upload class="upload"
                   action="/cmca/fileModelConfig/webUploader"
                   name="file"
                   :accept="category === 'Word' ? '.doc, .docx' : '.xlsx, .xls'"
                   :data="importData"
                   :show-file-list="false"
                   :before-upload="beforeUpload"
                   :on-success="importSuccess"
                   :on-error="importError">
          <el-button size="small"
                     type="primary"
                     :disabled="isUploading"> </el-button>
        </el-upload>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog center
               width="30%"
               title=""
               :close-on-click-modal="false"
               :close-on-press-escape="false"
               :visible.sync="dialogSetDefaultVisible">
      <p class="demonstration"></p>
      <div slot="footer"
           class="dialog-footer">
        <el-button size="small"
                   @click="dialogSetDefaultVisible = false"> </el-button>
        <el-button size="small"
                   type="primary"
                   @click="affirmSetDefault"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "MainTabContent",
  data() {
    return {
      // 
      searchForm: {
        subjectId: "",
        modelTypeId: ""
      },
      // 
      importForm: {
        subjectId: "",
        modelTypeId: "",
        fileNameId: ""
      },
      // 
      subjectList: [],
      // 
      tplTypeList: [],
      // 
      tableData: null,
      // 
      tableLoading: true,
      // -
      dialogFormVisible: false,
      // --
      importTplTypeList: [],
      // --
      importTplTypeDisabled: true,
      // -
      importTplFileList: [],
      // -
      importTplFileListDisabled: true,
      // -
      importDisableState: true,
      // -
      dialogUploadVisible: false,
      // --
      isUploading: false,
      // -
      dialogSetDefaultVisible: false,
      // -
      setDefaultVersionScope: null,
      // -
      importData: {
        category: "",
        rowCodeVersionId: ""
      }
    };
  },
  computed: mapState("fileModelConfig", {
    tableHeight: state => state.contentHeight - 80, // tab
    category: state => state.category // tab
  }),
  watch: {
    // -
    "searchForm.subjectId": {
      handler: function(newVal, oldVal) {
        // 
        if (oldVal) {
          this.$api.fileModelConfig
            .getTplTypeList({
              category: this.category,
              subjectId: this.searchForm.subjectId
            })
            .then(res => {
              res.modelType.unshift({
                modelTypeId: "all",
                modelTypeName: ""
              });
              this.searchForm.modelTypeId = "all";
              this.tplTypeList = res.modelType;
            })
            .catch(err => {
              this.$message.error(err);
            });
        }
      }
    },
    // --
    importForm: {
      handler: function(val, oldval) {
        if (val.subjectId && val.modelTypeId && val.fileNameId) {
          this.importDisableState = false;
        } else {
          this.importDisableState = true;
        }
      },
      deep: true
    },
    // --
    "importForm.subjectId": {
      handler: function(newVal, oldVal) {
        this.importTplTypeDisabled = true;
        this.importForm.modelTypeId = "";
        this.importTplTypeList = [];
        if (newVal) {
          this.$api.fileModelConfig
            .getTplTypeList({
              category: this.category,
              subjectId: this.importForm.subjectId
            })
            .then(res => {
              // 
              this.importTplTypeDisabled = false;
              if (res.modelType.length > 0) {
                this.importTplTypeList = res.modelType;
                this.importForm.modelTypeId = res.modelType[0].modelTypeId;
              }
            })
            .catch(err => {
              this.$message.error(err);
            });
        }
      }
    },
    // --&
    "importForm.modelTypeId": {
      handler: function(newVal, oldVal) {
        this.importTplFileListDisabled = true;
        this.importForm.fileNameId = "";
        this.importTplFileList = [];
        if (newVal) {
          this.loadTplFileList();
        }
      }
    }
  },
  methods: {
    // vuex
    ...mapActions("fileModelConfig", [
      "updateSubjectId", // 
      "ShowMainEdit", // 
      "updateEditTplInfo" // 
    ]),
    // 
    resetForm(formName) {
      this.importTplTypeDisabled = true;
      this.importTplFileListDisabled = true;
      this.$refs[formName].resetFields();
    },
    // 
    onSearch() {
      this.updateSubjectId(this.searchForm.subjectId);
      this.loadDetailTable();
    },
    // 
    loadDetailTable() {
      this.tableLoading = true;
      // 
      let postData = JSON.parse(JSON.stringify(this.searchForm));
      postData.category = this.category;
      // 
      this.$api.fileModelConfig
        .getDetailTableData(postData)
        .then(res => {
          // 
          res.tableData.forEach((lineData, i) => {
            lineData.selected = lineData.defaultVersionId;
          });
          // 
          this.tableData = res.tableData;
          this.tableLoading = false;
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    /**
     * tab
     * tab->+->->->+->
     * async+awaitPromise
     */
    async initTabContent() {
      // --
      let subjectListData = await this.$api.fileModelConfig
        .getSubjectList()
        .catch(err => {
          this.$message.error(err);
        });
      // 
      this.subjectList = subjectListData.subjectList;
      this.searchForm.subjectId = 4;
      // 
      this.updateSubjectId(this.searchForm.subjectId);
      // --
      let tplTypeListData = await this.$api.fileModelConfig
        .getTplTypeList({
          category: this.category,
          subjectId: this.searchForm.subjectId
        })
        .catch(err => {
          this.$message.error(err);
        });
      // 
      tplTypeListData.modelType.unshift({
        modelTypeId: "all",
        modelTypeName: ""
      });
      this.tplTypeList = tplTypeListData.modelType;
      this.searchForm.modelTypeId = "all";
      // 
      this.loadDetailTable();
    },
    /**
     * 
     *  / 
     * @param {object} scope scope http://element-cn.eleme.io/#/zh-CN/component/table#table-column-scoped-slot
     * @param {boolean} isDefault false
     */
    switchVersion(scope, isDefault) {
      let selectedVersionId = isDefault
        ? scope.row.defaultVersionId
        : scope.row.selected; // id
      // 
      this.$api.fileModelConfig
        .getTplVersionData({
          modelTypeId: this.searchForm.modelTypeId, // id
          rowCode: scope.row.rowCode, // id
          versionId: selectedVersionId // id
        })
        .then(res => {
          if (res.verisonData) {
            // 
            let rowData = JSON.parse(JSON.stringify(res.verisonData));
            // 
            for (const key in rowData) {
              if (rowData.hasOwnProperty(key)) {
                this.$set(this.tableData[scope.$index], key, rowData[key]);
              }
            }
          }
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    /**
     * -
     * @param {object} scope scope
     */
    eidtVersion(scope) {
      // 
      this.ShowMainEdit();
      // 
      this.updateEditTplInfo(scope.row);
    },
    /**
     * -
     * 1. 
     * 2. 
     * @param {object} scope scope
     */
    deleteVersion(scope) {
      if (scope.row.isDefault) {
        this.$alert("", "", {
          confirmButtonText: ""
        });
      } else {
        this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(() => {
          // 
          let postData = {
            rowCode: scope.row.rowCode,
            versionId: scope.row.selected
          };
          this.$api.fileModelConfig
            .deleteTplVersion(postData)
            .then(res => {
              if (res.deleteInfo) {
                this.$message({
                  type: "success",
                  message: "!"
                });
                // 
                let nowVersionList = this.tableData[scope.$index].versionList;
                nowVersionList.forEach((version, idx) => {
                  // 
                  if (version.id === scope.row.selected) {
                    nowVersionList.splice(idx, 1);
                  }
                });
                // 
                scope.row.selected = scope.row.defaultVersionId;
                this.switchVersion(scope, true);
              } else {
                this.$message({
                  type: "error",
                  message: "!"
                });
              }
            })
            .catch(err => {
              this.$message.error(err);
            });
        });
      }
    },
    // -
    downloadTpl(scope) {
      let rowCode = scope.row.rowCode;
      let versionId = scope.row.selected;
      let audTrm = scope.row.effectiveTime;
      window.open(
        `${
          this.$baseUrl
        }fileModelConfig/downLoadModelFile?rowCode=${rowCode}&versionId=${versionId}&category=${
          this.category
        }&audTrm=${audTrm.replace("-", "")}`
      );
    },
    // -
    setDefault(scope) {
      // id
      if (!scope.row.isDefault) {
        this.dialogSetDefaultVisible = true;
        this.setDefaultVersionScope = scope; // 
      }
    },
    // -
    affirmSetDefault() {
      // id
      let postData = {
        category: this.category,
        rowCode: this.setDefaultVersionScope.row.rowCode, // id
        versionId: this.setDefaultVersionScope.row.selected // 
      };
      this.$api.fileModelConfig
        .setDefaultTplVersion(postData)
        .then(res => {
          this.dialogSetDefaultVisible = false;
          // 
          this.switchVersion(this.setDefaultVersionScope);
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // --
    loadTplFileList() {
      if (this.importForm.subjectId && this.importForm.modelTypeId) {
        // 
        this.$api.fileModelConfig
          .getTplFileList({
            category: this.category,
            subjectId: this.importForm.subjectId,
            modelTypeId: this.importForm.modelTypeId
          })
          .then(res => {
            // 
            this.importTplFileListDisabled = false;
            if (res.fileList.length > 0) {
              this.importTplFileList = res.fileList;
              this.importForm.fileNameId = res.fileList[0].id;
            }
          })
          .catch(err => {
            this.$message.error(err);
          });
      } else {
        this.importTplFileList = null;
      }
    },
    // --
    confirmImport() {
      setTimeout(() => {
        this.dialogFormVisible = false;
        this.dialogUploadVisible = true;
        this.importData.rowCodeVersionId = this.importForm.fileNameId;
        this.importData.category = this.category;
      }, 0);
    },
    // --
    beforeUpload(file) {
      this.isUploading = true;
      let isFileType;
      let errTips;
      switch (this.category) {
        case "Word":
          isFileType =
            file.type === "application/msword" ||
            file.type ===
              "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
          errTips = ".docx,.doc";
          break;
        case "Excel":
          isFileType =
            file.type === "application/vnd.ms-excel" ||
            file.type ===
              "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
          errTips = ".xlsx,.xls";
          break;
      }
      if (!isFileType) {
        this.$alert(errTips, "", {
          confirmButtonText: "",
          callback: action => {
            this.isUploading = false;
            this.dialogFormVisible = true;
            this.dialogUploadVisible = false;
          }
        });
      }
      return isFileType;
    },
    // --
    importSuccess() {
      this.$message({
        type: "success",
        message: "!"
      });
      setTimeout(() => {
        // 
        this.loadDetailTable();
        this.isUploading = false;
        this.dialogUploadVisible = false;
      }, 0);
    },
    // --
    async importError(err) {
      this.$alert("", {
        confirmButtonText: "",
        callback: action => {
          this.isUploading = false;
          this.dialogFormVisible = true;
          this.dialogUploadVisible = false;
        }
      });
    }
  },
  mounted() {
    // tab
    this.initTabContent();
  }
};
</script>

<style lang="scss" scoped>
.main-tab-content {
  /deep/ .el-dialog {
    .upload {
      display: inline-block;
      margin-left: 20px;
    }
    .demonstration {
      margin-bottom: 10px;
      line-height: 30px;
    }
  }
  /deep/ .el-table__body {
    .el-button {
      padding: 7px;
    }
  }
}
</style>