<!-- - -->
<template>
  <div ref="editFormContainer" class="edit-form-container">
    <el-form ref="editForm" :model="editForm" label-width="120px" size="mini">
      <slot></slot>
      <el-form-item size="large" style="text-align:center">
        <el-button type="primary" size="small" @click="saveEdit"></el-button>
        <el-button size="small" @click="clearForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "EditForm",
  data() {
    return {
      // 
      isLoading: false,
      // 
      editForm: this.formModel
    };
  },
  props: {
    // id
    tplVersion: {
      required: true
    },
    // 
    formModel: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...mapState("fileModelConfig", {
      category: state => state.category, // 
      initEditData: state => state.editFormData, // &
      rowCode: state => state.editTplInfo.rowCode, // id
      blockData: state => state.blockData, // /-
      sheetOrder: state => state.sheetOrder // 
    }),
    // 
    newEditDataString() {
      return JSON.stringify(this.editForm);
    }
  },
  methods: {
    // vuex
    ...mapActions("fileModelConfig", [
      "initEditFormData" // 
    ]),
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // -
    saveEdit() {
      // 
      if (this.initEditData != this.newEditDataString) {
        let postData = JSON.parse(JSON.stringify(this.editForm));
        postData.rowCode = this.rowCode;
        postData.blockOrder = this.blockData.blockOrder;
        postData.sheetOrder = this.sheetOrder;
        postData.category = this.category;
        postData.versionId = this.tplVersion;
        this.$api.fileModelConfig
          .postEditData(postData)
          .then(res => {
            this.$message({
              message: "",
              type: "success"
            });
            // 
            this.initEditFormData(this.newEditDataString);
          })
          .catch(err => {
            this.$message.error(err);
          });
      } else {
        this.$message("");
      }
    },
    // -
    clearForm() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.resetForm("editForm");
        })
        .catch(() => {});
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.editFormContainer.scrollTop = 0;
    });
  }
};
</script>

<style lang="scss" scoped>
.edit-form-container {
  height: 100%;
  padding: 0 10px 0 30px;
  overflow-y: auto;
  overflow-x: hidden;
}
</style>