/** 
 * --
 */
import {
  mapState,
  mapActions
} from "vuex";

const rightControlMixin = {
  computed: {
    ...mapState({
      roleId: state => state.roleId, // 
      xqglAuthStatus: state => state.xqglAuthStatus, // 
      sjbgysAuthStatus: state => state.sjbgysAuthStatus, // 
      rightControl: state => state.rightControl // 
    })
  },
  methods: {
    // vuex
    ...mapActions([
      "initRoleId", // ID
      "initXqglAuthStatus", // ID
      "initSjbgysAuthStatus", // ID
      "initRightControl" // 
    ]),
    // 
    getRightControl() {
      // ---,3  2  1  / 
      this.$api
        .getLoginRole()
        .then(res => {
          this.initRoleId(res.roleId);
        })
        .catch(err => {
          this.$message.error(err);
        });
      // , 0  1 
      this.$api
        .getXqglControl()
        .then(res => {
          this.initXqglAuthStatus(res);
        })
        .catch(err => {
          this.$message.error(err);
        });
      // , 0  1 
      this.$api
        .getSjbgysControl()
        .then(res => {
          this.initSjbgysAuthStatus(res);
        })
        .catch(err => {
          this.$message.error(err);
        });
      // 
      this.$api
        .getRightControl()
        .then(res => {
          let rightControl = {};
          // 
          if (
            res.qdyksjjg ||
            res.qdykpmhz ||
            res.qdykqtcd ||
            res.qdykzgwz ||
            res.qdykwjxz
          ) {
            rightControl.yktlControl = true;
          } else {
            rightControl.yktlControl = false;
          }
          // 
          if (
            res.zdtlsjjg ||
            res.zdtlpmhz ||
            res.zdtlqtcd ||
            res.zdtlzgwz ||
            res.zdtlwjxz
          ) {
            rightControl.zdtlControl = true;
          } else {
            rightControl.zdtlControl = false;
          }
          // 
          if (
            res.yjk_sjjg ||
            res.yjk_pmhz ||
            res.yjk_qtcd ||
            res.yjk_zgwz ||
            res.yjk_wjxz
          ) {
            rightControl.yjkControl = true;
          } else {
            rightControl.yjkControl = false;
          }
          // 
          if (
            res.khqfsjjg ||
            res.khqfpmhz ||
            res.khqfqtcd ||
            res.khqfzgwz ||
            res.khqfwjxz
          ) {
            rightControl.khqfControl = true;
          } else {
            rightControl.khqfControl = false;
          }
          //
          if (
            res.ygycsjjg ||
            res.ygycpmhz ||
            res.ygycqtcd ||
            res.ygyczgwz ||
            res.ygycwjxz
          ) {
            rightControl.ygycczControl = true;
          } else {
            rightControl.ygycczControl = false;
          }
          // 
          if (
            res.dzqsjjg ||
            res.dzqpmhz ||
            res.dzqqtcd ||
            res.dzqzgwz ||
            res.dzqwjxz
          ) {
            rightControl.dzqControl = true;
          } else {
            rightControl.dzqControl = false;
          }
          // 
          if (
            res.llglsjjg ||
            res.llglpmhz ||
            res.llglqtcd ||
            res.llglzgwz ||
            res.llglwjxz
          ) {
            rightControl.llglControl = true;
          } else {
            rightControl.llglControl = false;
          }
          // 
          if (res.cxsjsjkg && this.roleId != 3) {
            rightControl.sjkgControl = true;
          } else {
            rightControl.sjkgControl = false;
          }
          // 
          if (res.cxsjcsgl) {
            rightControl.csglControl = true;
          } else {
            rightControl.csglControl = false;
          }
          // 
          if (res.cxsjsjbg) {
            rightControl.sjbgControl = true;
          } else {
            rightControl.sjbgControl = false;
          }
          // 
          if (res.cxsjsjjh) {
            rightControl.xqglControl = true;
          } else {
            rightControl.xqglControl = false;
          }
          // 
          if (res.cxsjsjjh && this.sjbgysAuthStatus != 0) {
            rightControl.sjbgysControl = true;
          } else {
            rightControl.sjbgysControl = false;
          }
          // 
          if (res.cxsjqxcx) {
            rightControl.qxcxControl = true;
          } else {
            rightControl.qxcxControl = false;
          }
          // -&
          if (res.cxsjsjgj) {
            rightControl.sjgjControl = true;
          } else {
            rightControl.sjgjControl = false;
          }
          // 
          if (res.cxsjrzcx) {
            rightControl.rzcxControl = true;
          } else {
            rightControl.rzcxControl = false;
          }
          // 
          if (res.cxsjsjzl) {
            rightControl.sjzlglControl = true;
          } else {
            rightControl.sjzlglControl = false;
          }
          // 
          if (res.cxsjsjrw) {
            rightControl.sjrwControl = true;
          } else {
            rightControl.sjrwControl = false;
          }
          // 
          if (res.cxsjwtfk) {
            rightControl.wtfkControl = true;
          } else {
            rightControl.wtfkControl = false;
          }
          // 
          if (res.cxsjsjtb) {
            rightControl.bgmbpzControl = true;
          } else {
            rightControl.bgmbpzControl = false;
          }
          // -
          rightControl.qfglControl = true;
          // -
          rightControl.zbglControl = true;
          // -
          rightControl.yxzyglControl = true;
          // -
          rightControl.kdglControl = true;
          // -
          rightControl.wlwglControl = true;
          // -
          rightControl.ykglControl = true;
          this.initRightControl(rightControl);
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // 
    navLocation(isControl, pageName) {
      if (isControl) {
        //  sessionStroage 
        sessionStorage.setItem("activePage", pageName);
        window.location.href = `${
          this.$baseUrl === "/" ? "/views/" : this.$baseUrl
        }${pageName}/index.html`;
      } else {
        alert("");
      }
    }
  },
  created() {
    this.getRightControl();
  }
};

export default rightControlMixin;