<!-- -tab -->
<template>
  <div class="main-content-tab">
    <el-tabs :style="{height : tabHeight + 'px'}"
             v-model="activeTab"
             tab-position="left"
             type="border-card"
             @tab-click="changeTab">
      <el-tab-pane name="Word">
        <span slot="label"><i class="iconfont icon-dq-text">&nbsp;</i></span>
        <!--  -->
        <tab-content v-if="WordTabVisiable"></tab-content>
      </el-tab-pane>
      <el-tab-pane name="Excel">
        <span slot="label"><i class="iconfont icon-baobiaobiaoweiguanli">&nbsp;</i></span>
        <!--  -->
        <tab-content v-if="ExcelTabVisiable"></tab-content>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import MainTabContent from "./MainTabContent";

export default {
  name: "MainTab",
  data() {
    return {
      // tab
      activeTab: "Word",
      WordTabVisiable: true,
      ExcelTabVisiable: false,
      // 
      typeList: {
        // 
        textTypes: [
          {
            id: "all",
            name: ""
          }
        ],
        // 
        statementTypes: [
          {
            id: "all",
            name: ""
          }
        ]
      }
    };
  },
  computed: {
    // tab
    tabHeight() {
      return this.$store.state.fileModelConfig.contentHeight;
    }
  },
  components: {
    TabContent: MainTabContent
  },
  methods: {
    // vuex
    ...mapActions("fileModelConfig", [
      "switchTab" // tab
    ]),
    // 
    initTab() {
      let tabName;
      if (this.activeTab === "Word") {
        this.WordTabVisiable = true;
        this.ExcelTabVisiable = false;
        tabName = "";
      } else {
        this.ExcelTabVisiable = true;
        this.WordTabVisiable = false;
        tabName = "";
      }
      this.switchTab({
        tabName: tabName,
        category: this.activeTab
      });
    },
    // 
    changeTab(tab) {
      // 
      let tabName = tab.$slots.label[0].elm.innerText.substring(1);
      // tabtab
      this.WordTabVisiable = true;
      this.ExcelTabVisiable = true;
      this.switchTab({
        tabName: tabName,
        category: this.activeTab
      });
    }
  },
  created() {
    // 
    this.initTab();
  }
};
</script>

<style lang="scss" scoped>
.main-content-tab {
  /deep/ .el-tabs__header {
    .el-tabs__item {
      height: 90px;
      width: 130px;
      line-height: 90px;
    }
  }
}
</style>
