<!--  -->
<template>
  <div class="main-pre-content"
       v-loading.fullscreen.lock="releaseLoading"
       element-loading-spinner="el-icon-loading"
       element-loading-text="">
    <el-row style="margin-bottom:10px">
      <el-col :span="6"
              :offset="18"
              style="text-align:right;">
        <el-button type="primary"
                   size="mini"
                   plain
                   :disabled="disabledState"
                   @click="releaseVersion"></el-button>
        <el-button type="primary"
                   plain
                   size="mini"
                   @click="downLoad"></el-button>
      </el-col>
    </el-row>
    <div ref="preTplPreview"
         v-loading="isLoading"
         element-loading-spinner="el-icon-loading"
         element-loading-text=""
         class="review-content"
         :style="{height : (mainHeight - 120) + 'px'}"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Compile } from "@/assets/js/common";

export default {
  name: "MainEditPreGenerated",
  data() {
    return {
      isLoading: true,
      disabledState: false,
      releaseLoading: false
    };
  },
  props: {
    tplVersion: {
      required: true
    },
    sheetOrder: {
      required: true
    },
    prvd: {
      required: true
    },
    audTrm: {
      required: true
    }
  },
  computed: {
    ...mapState("fileModelConfig", {
      mainHeight: state => state.contentHeight, // 
      category: state => state.category, // 
      rowCode: state => state.editTplInfo.rowCode // id
    })
  },
  methods: {
    // -
    loadPreTpl() {
      this.$refs.preTplPreview.scrollTop = 0;
      // 
      this.$api.fileModelConfig
        .getPreviewPreFile({
          rowCode: this.rowCode, // id
          category: this.category, // 
          versionId: this.tplVersion, // id
          sheetOrder: this.sheetOrder // id
        })
        .then(res => {
          // 
          this.isLoading = false;
          let EditTpl = Compile({
            template: `${res.preGenFile.txt}`,
            data() {
              return {
                style: res.preGenFile.style // DOM
              };
            }
          });
          // DOMDOM
          this.$refs.preTplPreview.innerHTML = "";
          this.$refs.preTplPreview.appendChild(EditTpl);
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // 
    releaseVersion() {
      this.releaseLoading = true;
      this.$api.fileModelConfig
        .releaseVersion({
          rowCode: this.rowCode, // id
          category: this.category, // 
          versionId: this.tplVersion // id
        })
        .then(res => {
          this.releaseLoading = false;
          if (res.setFullInfo) {
            this.disabledState = true;
            this.$message({
              type: "success",
              message: ``
            });
          } else {
            this.$alert("", "", {
              confirmButtonText: ""
            });
          }
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // 
    downLoad() {
      window.open(
        `${this.$baseUrl}fileModelConfig/downPreGenFile?rowCode=${
          this.rowCode
        }&category=${this.category}&versionId=${this.tplVersion}&prvdId=${
          this.prvd
        }&audTrm=${this.audTrm}`
      );
    }
  },
  mounted() {
    this.loadPreTpl();
  }
};
</script>
