<template>
  <the-layout>
    <div slot="main-container">
      <!--  -->
      <el-breadcrumb separator="\">
        <el-breadcrumb-item class="breadcrumb-active"></el-breadcrumb-item>
        <el-breadcrumb-item :class="{'breadcrumb-active':editContentVisible} "
                            @click.native="hideEditContent">
          {{ activeTabName }}
        </el-breadcrumb-item>
        <el-breadcrumb-item v-if="editContentVisible"></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-card class="main-body">
        <!-- -tab -->
        <main-tab v-show="!editContentVisible"></main-tab>
        <!-- - -->
        <main-edit v-if="editContentVisible"></main-edit>
      </el-card>
    </div>
  </the-layout>
</template>

<script>
import { mapState, mapActions } from "vuex";
import TheLayout from "component/TheLayout";
import MainTab from "./components/MainTab";
import MainEdit from "./components/MainEdit";

export default {
  name: "App",
  data() {
    return {};
  },
  computed: mapState("fileModelConfig", {
    activeTabName: state => state.activeTab, // tab
    editContentVisible: state => state.editContentVisible // -
  }),
  components: {
    TheLayout,
    MainTab,
    MainEdit
  },
  methods: {
    // vuex
    ...mapActions("fileModelConfig", [
      "HideMainEdit", // 
      "heightResize" // 
      ]),
    // 
    hideEditContent() {
      // 
      this.HideMainEdit();
    }
  },
  mounted() {
    // 
    this.heightResize(document.body.clientHeight);
    //  window  resize 
    const _this = this;
    window.onresize = () => {
      _this.heightResize(document.body.clientHeight);
    };
  }
};
</script>

<style lang="scss" scoped>
@import "style/color-theme.scss";
.main-container {
  .main-body {
    margin-top: 16px;
    /deep/ .el-card__body {
      padding: 0;
    }
  }
}
</style>
