<template>
  <div class="app-sidebar">
    <ul :class="isCollapse ? 'collapse-sidebar sidebar-nav-list' : 'sidebar-nav-list'">
      <!--  -->
      <li v-for="(lv1item, index) in navList"
          :key="index"
          class="sidebar-nav-item">
        <div class="sidebar-nav-sub-menu">
          <a href="javascript:;"
             :class="JSON.stringify(lv1item).indexOf(ActivePage) !== -1 ? 'active nav-item-icon' : 'nav-item-icon'"
             @mouseover="HoverSate(lv1item.index)"
             @mouseout="HoverSate(-1)"
             @click="navRoute(lv1item)">
            <span :class="lv1item.iconfont"></span>
            <!--  -->
            <span v-if="!isCollapse"
                  class="nav-content">{{ lv1item.name }}</span>
            <i v-if="!isCollapse"
               class="iconfont icon-point float-r"></i>
          </a>
          <transition name="fade">
            <a href="javascript:;"
               class="nav-item-tip"
               v-if="isCollapse"
               v-show="hoverActiveIndex === lv1item.index">
              <i class="iconfont icon-point"></i>{{ lv1item.name }}
            </a>
          </transition>
        </div>
        <!--  -->
        <el-collapse-transition>
          <ul v-if="lv1item.child"
              v-show="JSON.stringify(lv1item).indexOf(ActivePage) !== -1">
            <li v-for="(item, index) in lv1item.child"
                :key="index"
                class="sidebar-nav-item">
              <div class="sidebar-nav-child-menu">
                <a href="javascript:;"
                   :class="nowPage === item.index ? 'active nav-item-icon' : 'nav-item-icon'"
                   @mouseover="HoverSate(item.index)"
                   @mouseout="HoverSate(-1)"
                   @click="navRoute(item)">
                  <span v-if="isCollapse"
                        :class="item.iconfont"></span>
                  <span v-else
                        class="nav-content">{{ item.name }}</span>
                </a>
                <transition name="fade">
                  <a href="javascript:;"
                     class="nav-item-tip"
                     v-if="isCollapse"
                     v-show="hoverActiveIndex === item.index">
                    <i class="iconfont icon-point"></i>{{ item.name }}
                  </a>
                </transition>
              </div>
            </li>
          </ul>
        </el-collapse-transition>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
import rightControlMixin from "@/assets/js/rightControl";

export default {
  name: "TheSidebar",
  mixins: [rightControlMixin],
  data() {
    return {
      //  index 
      navList: [
        {
          index: "home",
          iconfont: "iconfont icon-shouye",
          name: ""
        },
        // 
        {
          index: "sjzt",
          iconfont: "iconfont icon-shenjizhuanti",
          name: "",
          // 
          child: [
            {
              index: "yktl",
              iconfont: "iconfont icon-yangkataoli",
              name: ""
            },
            {
              index: "zdtl",
              iconfont: "iconfont icon-zhongduantaol",
              name: ""
            },
            {
              index: "yjk",
              iconfont: "iconfont icon-youjiaqia",
              name: ""
            },
            {
              index: "khqf",
              iconfont: "iconfont icon-kehuqianfei",
              name: ""
            },
            {
              index: "ygyccz",
              iconfont: "iconfont icon-yuangongyichang",
              name: ""
            },
            {
              index: "dzqglwg",
              iconfont: "iconfont icon-dzqglwg",
              name: ""
            },
            {
              index: "llglwg",
              iconfont: "iconfont icon-llglwg",
              name: ""
            }
          ]
        },
        // 
        {
          index: "sjgj",
          iconfont: "iconfont icon-shenjigenjin",
          name: "",
          // 
          child: [
            {
              index: "zgxx",
              iconfont: "iconfont icon-zhenggaixinxi",
              name: ""
            },
            {
              index: "wzxx",
              iconfont: "iconfont icon-wenzexinxi",
              name: ""
            }
          ]
        },
        // 
        {
          index: "ywgl",
          iconfont: "iconfont icon-yewuguanli",
          name: "",
          // 
          child: [
            {
              index: "xqgl",
              iconfont: "iconfont icon-fabuxuqiu",
              name: ""
            },
            {
              index: "sjkg",
              iconfont: "iconfont icon-shenjikaiguan",
              name: ""
            },
            {
              index: "sjbg",
              iconfont: "iconfont icon-baogaoxaizai",
              name: ""
            },
            {
              index: "csgl",
              iconfont: "iconfont icon-canshuguanli",
              name: ""
            }
          ]
        },
        // 
        {
          index: "fxjk",
          iconfont: "iconfont icon-yewugjianongjiyujing",
          name: "",
          // 
          child: [
            {
              index: "qfgl",
              iconfont: "iconfont icon-arrears-status",
              name: ""
            },
            {
              index: "zbgl",
              iconfont: "iconfont icon-zhangben",
              name: ""
            },
            {
              index: "yxzygl",
              iconfont: "iconfont icon-canshuguanli",
              name: ""
            },
            {
              index: "kdgl",
              iconfont: "iconfont icon-kuandaitianchong",
              name: ""
            },
            {
              index: "wlwgl",
              iconfont: "iconfont icon-iotwulianwang",
              name: ""
            },
            {
              index: "ykgl",
              iconfont: "iconfont icon-ykgl",
              name: ""
            }
          ]
        },
        // 
        {
          index: "grgzq",
          iconfont: "iconfont icon-gerengongzuoqu",
          name: "",
          // 
          child: [
            {
              index: "bgys",
              iconfont: "iconfont icon-shenjibaogaoyushen",
              name: ""
            },
            {
              index: "qxcx",
              iconfont: "iconfont icon-quanxianchaxun",
              name: ""
            },
            {
              index: "rzcx",
              iconfont: "iconfont icon-rizhi",
              name: ""
            },
            {
              index: "sjzlgl",
              iconfont: "iconfont icon-shujuzhiliang",
              name: ""
            },
            {
              index: "wtfk",
              iconfont: "iconfont icon-question-feedba",
              name: ""
            },
            {
              index: "sjrw",
              iconfont: "iconfont icon-gongrengongzuoqu",
              name: ""
            },
            {
              index: "fileModelConfig",
              iconfont: "iconfont icon-shujuzhiliang",
              name: ""
            }
          ]
        }
      ],
      hoverActiveIndex: -1, // 
      clientWidth: document.body.clientWidth, // 
      nowPage: sessionStorage.getItem("activePage"), // 
      ActivePage: sessionStorage.getItem("activePage"), // sessionStroage 
      isCollapse: Boolean // 
    };
  },
  computed: mapState(["rightControl"]),
  methods: {
    // 
    HoverSate(index) {
      this.hoverActiveIndex = index;
    },
    // 
    navRoute(navItem) {
      this.ActivePage = navItem.index;
      let pageName = navItem.index;
      // 
      if (!navItem.child && this.nowPage != pageName) {
        switch (pageName) {
          case "home": // 
            this.navLocation(true, "home");
            break;
          case "yktl": // 
            this.navLocation(this.rightControl.yktlControl, "yktl");
            break;
          case "zdtl": // 
            this.navLocation(this.rightControl.zdtlControl, "zdtl");
            break;
          case "yjk": // 
            this.navLocation(this.rightControl.yjkControl, "yjk");
            break;
          case "khqf": //
            this.navLocation(this.rightControl.khqfControl, "khqf");
            break;
          case "ygyccz": //
            this.navLocation(this.rightControl.ygycczControl, "ygyccz");
            break;
          case "sjkg": //
            this.navLocation(this.rightControl.sjkgControl, "sjkg");
            break;
          case "csgl": //
            this.navLocation(this.rightControl.csglControl, "csgl");
            break;
          case "sjbg": //
            this.navLocation(this.rightControl.sjbgControl, "sjbg");
            break;
          case "qfgl": //-
            this.navLocation(this.rightControl.qfglControl, "qfgl");
            window.sessionStorage.setItem("domain1", "lp1_qfgl");
            window.sessionStorage.setItem("domain2", "2khqkgl");
            window.sessionStorage.setItem("mon", "");
            break;
          case "zbgl": //-
            this.navLocation(this.rightControl.zbglControl, "zbgl");
            window.sessionStorage.setItem("domain1", "lp1_zbgl");
            window.sessionStorage.setItem("domain2", "2hfzs/tfgl");
            window.sessionStorage.setItem("mon", "");
            break;
          case "yxzygl": //-
            //  sessionStroage 
            this.navLocation(this.rightControl.yxzyglControl, "yxzygl");
            window.sessionStorage.setItem("domain1", "lp1_yxzygl");
            window.sessionStorage.setItem("domain2", "2yjkgl");
            window.sessionStorage.setItem("mon", "");
            break;
          case "kdgl": //-
            this.navLocation(this.rightControl.kdglControl, "kdgl");
            window.sessionStorage.setItem("domain1", "lp1_kdgl");
            window.sessionStorage.setItem("domain2", "2jtkdgl");
            window.sessionStorage.setItem("mon", "");
            break;
          case "wlwgl": //-
            this.navLocation(this.rightControl.wlwglControl, "wlwgl");
            window.sessionStorage.setItem("domain1", "lp1_wlwgl");
            window.sessionStorage.setItem("domain2", "2wlkgl");
            window.sessionStorage.setItem("mon", "");
            break;
          case "ykgl": //-
            this.navLocation(this.rightControl.ykglControl, "ykgl");
            window.sessionStorage.setItem("domain1", "lp1_ykgl");
            window.sessionStorage.setItem("domain2", "2qdllyk");
            window.sessionStorage.setItem("mon", "");
            break;
          case "dzqglwg": //
            this.navLocation(this.rightControl.dzqControl, "dzqglwg");
            break;
          case "llglwg": //
            this.navLocation(this.rightControl.llglControl, "llglwg");
            break;
          case "xqgl": //
            this.navLocation(this.rightControl.xqglControl, "xqgl");
            break;
          case "bgys": //
            this.navLocation(this.rightControl.sjbgysControl, "sjbgys");
            break;
          case "qxcx": //
            this.navLocation(this.rightControl.qxcxControl, "qxcx");
            break;
          case "wzxx": //
            if (this.rightControl.sjgjControl) {
              //  sessionStroage 
              sessionStorage.setItem("activePage", pageName);
              window.location.href = `${
                this.$baseUrl === "/" ? "/views/" : this.$baseUrl
              }sjgj/${pageName}.html`;
            } else {
              alert("");
            }
            break;
          case "zgxx": //
            if (this.rightControl.sjgjControl) {
              //  sessionStroage 
              sessionStorage.setItem("activePage", pageName);
              window.location.href = `${
                this.$baseUrl === "/" ? "/views/" : this.$baseUrl
              }sjgj/${pageName}.html`;
            } else {
              alert("");
            }
            break;
          case "rzcx": //
            this.navLocation(this.rightControl.rzcxControl, "rzcx");
            break;
          case "sjzlgl": // 
            this.navLocation(this.rightControl.sjzlglControl, "sjzlgl");
            break;
          case "sjrw": // 
            this.navLocation(this.rightControl.sjrwControl, "sjrw");
            break;
          case "wtfk": //
            this.navLocation(this.rightControl.wtfkControl, "wtfk");
            break;
          case "fileModelConfig": // 
            this.navLocation(
              this.rightControl.bgmbpzControl,
              "fileModelConfig"
            );
            break;
          default:
            //
            alert("");
            break;
        }
      }
    }
  },
  created() {
    // DOMDOM
    if (this.clientWidth <= 1366) {
      this.isCollapse = true;
    } else {
      this.isCollapse = false;
    }
  },
  mounted() {
    // 
    window.addEventListener("resize", event => {
      this.clientWidth = document.body.clientWidth;
      // 
      if (this.clientWidth <= 1366) {
        this.isCollapse = true;
      } else {
        this.isCollapse = false;
      }
    });
  }
};
</script>

<style lang="scss" scoped>
// 
.fade-enter,
.fade-leave-to {
  opacity: 0;
  transform: translateX(-100%);
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s ease;
}
// 
.app-sidebar {
  display: inline-block;
  height: 100%;
  background-color: #333f4e;
  .sidebar-nav-item {
    .sidebar-nav-sub-menu,
    .sidebar-nav-child-menu {
      position: relative;
    }
    a {
      display: block;
      height: 40px;
      line-height: 40px;
      background-color: #333f4e;
      color: #fff;
    }
    .nav-content {
      white-space: nowrap;
    }
    .sidebar-nav-sub-menu {
      a.nav-item-icon {
        border-top: 1px solid #1a1b24;
        white-space: nowrap;
        .iconfont {
          font-size: 20px;
        }
      }
    }
    .sidebar-nav-child-menu {
      a.nav-item-icon {
        color: #8b9ab7;
      }
      a.active,
      a.nav-item-icon:hover {
        color: #fff;
      }
    }
  }
  .sidebar-nav-item:last-of-type {
    .sidebar-nav-sub-menu {
      a.nav-item-icon {
        border-bottom: 1px solid #1a1b24;
      }
    }
  }
  .sidebar-nav-list {
    transition: width 0.2s ease;
  }
  // 
  .sidebar-nav-list:not(.collapse-sidebar) {
    width: 200px;
    .nav-item-icon {
      color: #8b9ab7;
      .iconfont {
        display: inline-block;
        padding: 0 10px;
      }
    }
    .nav-item-icon.active {
      color: #fff;
    }
    .sidebar-nav-sub-menu {
      .nav-item-icon {
        i.iconfont {
          font-size: 14px;
          transition: all 0.2s ease;
        }
      }
      .nav-item-icon.active {
        i.iconfont {
          transform: rotateZ(-90deg);
        }
      }
    }
    .sidebar-nav-child-menu {
      a {
        height: 30px;
        line-height: 30px;
        padding-left: 30px;
        font-size: 12px;
      }
      .nav-item-icon.active {
        padding-left: 24px;
        border-left: 6px solid #f8a900;
        color: #fff;
        background-color: #1a1b24;
      }
    }
  }
  // 
  .collapse-sidebar {
    width: 50px;
    .nav-item-icon {
      z-index: 99;
      position: relative;
      text-align: center;
    }
    .nav-item-icon.active {
      background-color: #1a1b24;
    }
    a.nav-item-tip {
      z-index: 98;
      position: absolute;
      top: 0;
      left: 50px;
      padding-right: 10px;
      white-space: nowrap;
      text-align: left;
      background-color: #333f4e;
    }
    .sidebar-nav-sub-menu {
      .nav-item-icon.active {
        border-left: 6px solid #f8a900;
        background-color: #1a1b24;
      }
    }
  }
}
</style>
