<!-- - -->
<template>
  <div class="reuse-container">
    <el-form size="mini"
             :inline="true"
             :model="reuseForm"
             label-width="70px">
      <el-row>
        <el-col :span="category === 'Excel' ? 8 : 10">
          <el-form-item label="">
            <el-select v-model="reuseForm.versionId">
              <el-option v-for="(item, index) in tplVersionList"
                         :key="index"
                         :label="item.name"
                         :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="category === 'Excel' ? 8 : 0"
                v-if="category === 'Excel'">
          <el-form-item label=""
                        v-if="category === 'Excel'">
            <el-select v-model="reuseForm.sheetOrder">
              <el-option v-for="(item, index) in sheetList"
                         :key="index"
                         :label="item.sheetName"
                         :value="item.sheetOrder">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="category === 'Excel' ? 8 : 10">
          <el-form-item label="">
            <el-select v-model="reuseForm.blockOrder">
              <el-option v-for="(item, index) in blockList"
                         :key="index"
                         :label="item.blockName"
                         :value="item.blockOrder">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div v-loading="isLoading"
         element-loading-spinner="el-icon-loading"
         element-loading-text=""
         class="review-content"
         ref="reusePrview">
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Compile } from "@/assets/js/common";

export default {
  name: "ReuseContent",
  data() {
    return {
      // 
      reuseForm: {
        // 
        versionId: "",
        // 
        sheetOrder: "",
        // 
        blockOrder: ""
      },
      // 
      sheetList: [
        {
          sheetOrder: 1,
          sheetName: "test"
        }
      ],
      // 
      blockList: [],
      // 
      isLoading: false
    };
  },
  computed: mapState("fileModelConfig", {
    category: state => state.category, // 
    rowCode: state => state.editTplInfo.rowCode, // id
    tplVersionList: state => state.editTplInfo.versionList, // 
    defaultTplVersion: state => state.editTplInfo.defaultVersionId, // -
    blockData: state => state.blockData // /-
  }),
  watch: {
    // -/
    "reuseForm.versionId"(newVal, oldVal) {
      if (this.category === "Excel") {
        this.$api.fileModelConfig
          .getSheetList({
            rowCode: this.rowCode,
            versionId: this.reuseForm.versionId
          })
          .then(res => {
            // 
            this.sheetList = res.sheetNameData;
            this.reuseForm.sheetOrder = res.sheetNameData[0].sheetOrder;
            // 
            this.loadReuseTplPreview();
          });
      } else {
        // 
        this.loadReuseTplPreview();
      }
    },
    // -
    "reuseForm.sheetOrder"(newVal, oldVal) {
      // oldVal
      if (oldVal) {
        this.loadReuseTplPreview();
      }
    },
    // reuseForm
    "reuseForm.blockOrder"(newVal, oldVal) {
      /**
       * 1. operationedit
       * 2. 
       */
      this.$confirm(``, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          let postData = JSON.stringify(JSON.parse(this.reuseForm));
          postData.category = this.category;
          postData.rowCode = this.rowCode;
          this.$api.fileModelConfig
            .getEditFormData(postData)
            .then(res => {
              /* 
                ()
                async
              */
              let _this = this;
              async function updateEditFormSate() {
                await _this.updateTplOperation("edit");
                _this.initReuseEditData(res.editInfoData);
              }
              updateEditFormSate();
            })
            .catch(err => {
              this.$message.error(err);
            });
        })
        .catch(() => {});
    },
    // 
    blockData: {
      handler: function(newVal, oldVal) {
        if (newVal.blockOrder !== oldVal.blockOrder) {
          // 
          this.reuseForm.versionId = this.defaultTplVersion;
        }
      },
      deep: true
    }
  },
  methods: {
    // vuex
    ...mapActions("fileModelConfig", [
      "initReuseEditData", // 
      "updateTplOperation" //  edit/reuse
    ]),
    // 
    loadReuseTplPreview() {
      this.isLoading = true;
      this.$refs.reusePrview.innerHTML = "";
      this.$nextTick(() => {
        // 
        this.$api.fileModelConfig
          .getReusePreviewFile({
            versionId: this.reuseForm.versionId, // id
            sheetOrder: this.reuseForm.sheetOrder, // id
            rowCode: this.rowCode, // id
            category: this.category // 
          })
          .then(res => {
            // 
            this.$refs.reusePrview.scrollTop = 0;
            let tpl;
            if (res.txtHandleTwo.txt) {
              tpl = res.txtHandleTwo.txt;
            } else {
              tpl = `<p style="text-align:center"></p>`;
            }
            /*
             html
             htmlDOM
             */
            let EditTpl = Compile({
              template: `${tpl}`,
              data() {
                return {
                  style: res.txtHandleTwo.style
                };
              }
            });
            // 
            this.$refs.reusePrview.appendChild(EditTpl);
            this.reuseForm.blockOrder = "";
            this.blockList = res.txtHandleTwo.blockOrder;
            this.isLoading = false;
          })
          .catch(err => {
            this.$message.error(err);
          });
      });
    }
  },
  created() {
    // 
    this.reuseForm.versionId = this.defaultTplVersion;
  }
};
</script>

<style lang="scss" scoped>
.reuse-container {
  height: 100%;
  /deep/ .review-content {
    height: calc(100% - 50px);
  }
  /deep/ .el-form-item__content {
    width: 60%;
  }
}
</style>
