<!-- - -->
<template>
  <el-dialog title=""
             :visible="updateDialogVisible"
             :close-on-click-modal="false"
             :close-on-press-escape="false"
             @open="openUpdateDialog"
             :before-close="closeTip"
             class="edit-dialog">
    <el-row :style="{height : (mainHeight - 90) + 'px'}">
      <!--  -->
      <el-col :span="isOperation ? 12 : 24"
              style="height:100%">
        <div ref="editTplPreview"
             v-loading="isUpdateTplLoading"
             element-loading-spinner="el-icon-loading"
             element-loading-text=""
             class="review-content"></div>
      </el-col>
      <!-- / -->
      <el-col v-if="isOperation"
              v-loading="operationlLoading"
              element-loading-spinner="el-icon-loading"
              element-loading-text=""
              :span="isOperation ? 12 : 0"
              style="height:100%">
        <!-- - -->
        <edit-form ref="editForm"
                   v-if="operation === 'edit' && category === 'Word'"
                   :tpl-version="tplVersion"
                   :form-model="wordEditForm">
          <form-item-title iconColor="#FE9455"></form-item-title>
          <el-form-item prop="mergeLast">
            <el-checkbox v-model="wordEditForm.mergeLast"
                         :true-label="1"
                         :false-label="0"></el-checkbox>
          </el-form-item>
          <form-item-title iconColor="#AD54FF"></form-item-title>
          <el-form-item prop="querySql"
                        label="SQL">
            <el-input v-model="wordEditForm.querySql"
                      type="textarea"
                      :rows="5"></el-input>
          </el-form-item>
          <form-item-title></form-item-title>
          <el-form-item prop="showType"
                        label="">
            <el-select v-model="wordEditForm.showType"
                       placeholder="">
              <el-option v-for="(item, index) in showTypeList"
                         :key="index"
                         :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <transition name="el-zoom-in-top">
            <el-form-item prop="showSql"
                          label="SQL"
                          v-show="wordEditForm.showType === 1">
              <el-input v-model="wordEditForm.showSql"
                        type="textarea"
                        :rows="5"></el-input>
            </el-form-item>
          </transition>
        </edit-form>
        <!-- - -->
        <edit-form ref="editForm"
                   v-if="operation === 'edit' && category === 'Excel'"
                   :tpl-version="tplVersion"
                   :form-model="excelEditForm">
          <form-item-title iconColor="#FE9455"></form-item-title>
          <el-row>
            <el-col :span="10">
              <el-form-item v-if="subjectId === 9"
                            prop="audTrmOption"
                            label="">
                <el-select v-model="excelEditForm.audTrmOption">
                  <el-option v-for="(item, index) in audTrmOptionList"
                             :key="index"
                             :label="item.name"
                             :value="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="10">
              <el-form-item prop="isWideTb">
                <el-checkbox v-model="excelEditForm.isWideTb"
                             :true-label="1"
                             :false-label="0"></el-checkbox>
              </el-form-item>
            </el-col>
          </el-row>
          <transition name="el-zoom-in-top">
            <el-row v-if="excelEditForm.isWideTb">
              <el-col :span="10">
                <el-form-item prop="wideTbMonth"
                              label="">
                  <el-input v-model="excelEditForm.wideTbMonth"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="10">
                <el-form-item prop="wideTbCol"
                              label="">
                  <el-input v-model="excelEditForm.wideTbCol"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </transition>
          <form-item-title iconColor="#AD54FF"></form-item-title>
          <el-form-item prop="querySql"
                        label="SQL">
            <el-input v-model="excelEditForm.querySql"
                      type="textarea"
                      :rows="5"></el-input>
          </el-form-item>
        </edit-form>
        <edit-reuse v-if="operation === 'reuse'"></edit-reuse>
      </el-col>
    </el-row>
  </el-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Compile } from "@/assets/js/common";
import FormItemTitle from "./VFormTitle";
import EditReuse from "./MainEditUpdateDialogReuse";
import EditForm from "./MainEditUpdateDialogForm";

export default {
  name: "MainEditUpdateDialog",
  data() {
    return {
      // --loading
      isUpdateTplLoading: true,
      // --
      isOperation: false,
      // --
      operationlLoading: true,
      // --
      wordEditForm: {
        querySql: "", // sql
        showSql: "", // sql
        mergeLast: 0, // 
        showType: 0 // 
      },
      // --
      showTypeList: [
        {
          id: 0,
          name: "0-"
        },
        {
          id: 1,
          name: "1-SQL"
        }
      ],
      // --
      excelEditForm: {
        audTrmOption: "", // 
        isWideTb: 0, // 
        wideTbMonth: "", // 
        wideTbCol: "", // ,
        querySql: "" // SQL
      },
      // --
      audTrmOptionList: [
        {
          id: -1,
          name: "-1"
        },
        {
          id: -2,
          name: "-2"
        },
        {
          id: -3,
          name: "-3"
        }
      ]
    };
  },
  computed: {
    ...mapState("fileModelConfig", {
      mainHeight: state => state.contentHeight, // 
      subjectId: state => state.subjectId, // 
      category: state => state.category, // 
      updateDialogVisible: state => state.updateDialogVisible, // 
      rowCode: state => state.editTplInfo.rowCode, // id
      operation: state => state.tplOperation, //  edit/reuse
      initEditData: state => state.editFormData, // &
      blockData: state => state.blockData, // /-
      reuseEditData: state => state.reuseEditData // -
    }),
    // 
    // TODO: 
    editFormModel() {
      return this.category === "Word" ? this.wordEditForm : this.excelEditForm;
    },
    // 
    newEditDataString() {
      return JSON.stringify(this.editFormModel);
    }
  },
  props: {
    tplVersion: {
      required: true
    },
    sheetOrder: {
      required: true
    }
  },
  watch: {
    // 
    updateDialogVisible(newVal, oldVal) {
      if (!newVal) {
        // 
        this.$refs.editTplPreview.innerHTML = "";
        // 
        this.isOperation = false;
        // 
        this.initEditFormData(null);
      }
    },
    // 
    blockData: {
      handler: function(newVal, oldVal) {
        this.initEditForm();
      },
      deep: true
    },
    // 
    reuseEditData: {
      handler: function(newVal, oldVal) {
        // 
        for (const key in this.editFormModel) {
          if (
            this.editFormModel.hasOwnProperty(key) &&
            newVal.hasOwnProperty(key)
          ) {
            this.editFormModel[key] = newVal[key];
          }
        }
      },
      deep: true
    }
  },
  components: {
    FormItemTitle,
    EditForm,
    EditReuse
  },
  methods: {
    // vuex
    ...mapActions("fileModelConfig", [
      "hideUpdateDialogVisible", // 
      "updateTplOperation", //  edit/reuse
      "initEditFormData", // 
      "updateBlockData" // /-
    ]),
    // -
    editSaveTip() {
      return this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      });
    },
    // &htmlDOM
    loadEditPreviewFile() {
      this.$refs.editTplPreview.scrollTop = 0;
      // 
      this.$api.fileModelConfig
        .getEditPreviewFile({
          versionId: this.tplVersion, // id
          rowCode: this.rowCode, // id
          category: this.category, // 
          sheetOrder: this.sheetOrder // id
        })
        .then(res => {
          // 
          this.isUpdateTplLoading = false;
          let _this = this;
          /**
           * html
           * htmlDOMhtml
           */
          let EditTpl = Compile({
            template: `${res.txtHandleOne.txt}`,
            data() {
              return {
                style: res.txtHandleOne.style, // DOM
                blockInfo: null
              };
            },
            methods: {
              /**
               * /-
               * 1. 
               * 1.1 
               * 1.2 
               * 1.2.1 
               * 1.2.1.1 (/)
               * 1.2.2 
               * 1.2.2.1 
               * 1.2.2.1.1 
               * 1.2.2.1.2 (/)
               * 2. 
               */
              switchBlock(blockData, operation) {
                this.blockInfo = {
                  blockOrder: blockData[0],
                  sheetOrder: blockData[1]
                };
                if (_this.isOperation) {
                  if (_this.blockData.blockOrder !== blockData[0]) {
                    if (_this.initEditData !== _this.newEditDataString) {
                      _this
                        .editSaveTip()
                        .then(() => {
                          _this.updateBlockData(this.blockInfo);
                          if (operation !== _this.operation) {
                            _this.updateTplOperation(operation);
                          }
                        })
                        .catch(() => {});
                    } else {
                      _this.updateBlockData(this.blockInfo);
                      if (operation !== _this.operation) {
                        _this.updateTplOperation(operation);
                      }
                    }
                  } else {
                    if (operation !== _this.operation) {
                      _this.updateTplOperation(operation);
                    }
                  }
                } else {
                  _this.isOperation = true;
                  _this.updateBlockData(this.blockInfo);
                  _this.updateTplOperation(operation);
                }
                // if (_this.initEditData) {
                //   if (_this.initEditData !== _this.newEditDataString) {
                //     if (_this.blockData.blockOrder !== blockData[0]) {
                //       _this
                //         .editSaveTip()
                //         .then(() => {
                //           _this.updateTplOperation(operati~on);
                //           _this.updateBlockData(this.blockInfo);
                //         })
                //         .catch(() => {});
                //     } else {
                //       _this.updateTplOperation(operation);
                //     }
                //   } else {
                //     _this.updateTplOperation(operation);
                //     if (_this.blockData.blockOrder !== blockData[0]) {
                //       _this.updateBlockData(this.blockInfo);
                //     }
                //   }
                // } else {
                //   _this.updateBlockData(this.blockInfo);
                //   _this.updateTplOperation(operation);
                // }
              },
              // 
              edit(...blockData) {
                this.switchBlock(blockData, "edit");
              },
              // 
              reuse(...blockData) {
                this.switchBlock(blockData, "reuse");
              }
            }
          });
          // DOMDOM
          this.$refs.editTplPreview.innerHTML = "";
          this.$refs.editTplPreview.appendChild(EditTpl);
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // -
    openUpdateDialog() {
      // loading
      this.isUpdateTplLoading = true;
      // 
      this.isOperation = false;
      // DOM
      this.$nextTick(() => {
        this.loadEditPreviewFile();
      });
    },
    // --
    async closeTip(done) {
      let state = true;
      if (this.isOperation && this.operation === "edit") {
        if (this.initEditData !== this.newEditDataString) {
          state = await this.editSaveTip()
            .then(() => {
              return true;
            })
            .catch(() => {
              return false;
            });
        }
      }
      if (state) {
        this.hideUpdateDialogVisible();
      }
    },
    // ---
    initEditForm() {
      this.operationlLoading = true;
      // 
      this.$api.fileModelConfig
        .getEditFormData({
          category: this.category, // 
          rowCode: this.rowCode, // id
          versionId: this.tplVersion, // id
          blockOrder: this.blockData.blockOrder, // id
          sheetOrder: this.sheetOrder // sheetid
        })
        .then(res => {
          let formData;
          if (res.editInfoData) {
            formData = res.editInfoData;
          } else {
            formData = {
              querySql: "", // sql
              showSql: "", // sql
              mergeLast: 0, // 
              showType: 0, // 
              audTrmOption: "", // 
              isWideTb: 0, // 
              wideTbMonth: "", // 
              wideTbCol: "", // ,
              querySql: "" // SQL
            };
          }
          /*
            
            
            1. 
            2. 
          */
          let initEditFormData = {};
          if (this.editFormModel) {
            if (this.operation === "edit") {
              for (const key in this.editFormModel) {
                if (
                  this.editFormModel.hasOwnProperty(key) &&
                  formData.hasOwnProperty(key)
                ) {
                  this.editFormModel[key] = formData[key];
                }
              }
              initEditFormData = this.editFormModel;
            } else {
              for (const key in this.editFormModel) {
                if (
                  this.editFormModel.hasOwnProperty(key) &&
                  formData.hasOwnProperty(key)
                ) {
                  initEditFormData[key] = formData[key];
                }
              }
            }
            this.initEditFormData(JSON.stringify(initEditFormData));
            this.operationlLoading = false;
          }
        })
        .catch(err => {
          this.$message.error(err);
        });
    }
  }
};
</script>
