/**
 * api 
 */
import axios from './axios';
// api
import fileModelConfig from './modules/fileModelConfig';

// 
// ---
const getLoginRole = () => {
  return axios.get("/bgxz/index");
};
// 
const getXqglControl = () => {
  return axios.get("/xqgl/getAuthorityAttr");
}
// 
const getSjbgysControl = () => {
  return axios.get("/sjbgys/getAuthStatus");
}
// 
const getRightControl = () => {
  return axios.get("/home/getRight");
}

export default {
  getLoginRole,
  getXqglControl,
  getSjbgysControl,
  getRightControl,
  fileModelConfig
}